#loading {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  font-size: 20px;
}

@primary-color: #1890ff;@success-color: #5ecdc4;@warning-color: #6d4cc2;@error-color: #e64a19;